.effective {
  margin-top: 70px;

  & .title {
    text-align: center;
  }
}

.effective__list {
  @include reset-list;
  display: flex;
  justify-content: space-between;
  margin-top: 50px;
}

.effective__item {
  display: flex;
}

.effective__item-number {
  text-align: center;
  padding-top: 25px;
  font-size: 30px;
  font-weight: 700;
  color: $basic-red;
  background-repeat: no-repeat;
  background-position: center;
  align-self: center;
  width: 100px;
  height: 100px;
  margin-right: 20px;
}

.effective__item-number--60 {
  background-image: url("../img/60.svg");
}

.effective__item-number--29 {
  background-image: url("../img/29.svg");
}

.effective__item-number--15 {
  background-image: url("../img/15.svg");
}

.effective__description {
  width: 80%;
  margin: 0 auto;
  margin-top: 80px;
  padding: 30px;
  border-radius: 5px;
  background-color: $light-grey;
}

.effective__description-text span {
  color: $basic-red;
  font-weight: 500;
}
