.testemonials {
  margin-top: 70px;
  padding: 70px 0;
  background-color: $light-grey;

  & .title {
    text-align: center;
  }
}

.carousel-inner {
  margin-top: 50px;
}

.carousel-item {
  width: 80%;
  margin: 0 auto;
  position: relative;
  background-color: $basic-white;
  border-radius: 5px;
  padding: 40px;
}

.testemonial--wrapper {
  display: flex;
  align-items: center;
}

.user-info {
  @include main-text;
  margin-left: 20px;

  & p {
    margin-bottom: 5px;
  }
}

.user-info__name {
  font-weight: 600;
  margin-bottom: 5px;
}

.testemonial-text {
  margin-top: 20px;
  margin-bottom: 0;
}

.prev-icon {
  position: absolute;
  top: 50%;
  left: 45px;
  width: 20px;
  height: 20px;
  background-image: url("../img/arrow-left.svg");
  background-position: center;
  background-repeat: no-repeat;
}

.next-icon {
  position: absolute;
  top: 50%;
  right: 20px;
  width: 20px;
  height: 20px;
  background-image: url("../img/arrow-right.svg");
  background-position: center;
  background-repeat: no-repeat;
}
