.request {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column-gap: 50px;
  margin-top: 80px;

  &::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 320px;
    background-color: $light-grey;
    top: 80px;
    z-index: -1;
  }

  & .form__title {
    background-color: transparent;
    padding: 0;
  }

  & .form-price {
    margin-top: 20px;
  }
}

.request__info {
  margin-top: 160px;
  margin-left: 30px;
}

.request__form {
  margin-top: 100px;
  margin-right: 30px;
}
