.ingredients {
  margin-top: 70px;
}

.ingredients__list {
  @include reset-list;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-top: 50px;
}

.ingredients__item {
  width: 48%;
  padding-left: 120px;
  margin-bottom: 40px;
  background-position: top left;
  background-repeat: no-repeat;

  &:first-child {
    background-image: url("../img/plant.png");
  }

  &:nth-child(2) {
    background-image: url("../img/plant2.png");
  }

  &:nth-child(3) {
    background-image: url("../img/plant3.png");
  }

  &:nth-child(4) {
    background-image: url("../img/plant4.png");
  }

  &:nth-child(5) {
    background-image: url("../img/plant5.png");
  }
}

.ingredients__item-header {
  font-size: 18px;
}

.ingredients__item-subheader {
  @include main-text;
}

.ingredients__text {
  text-align: center;
  @include main-text;
  font-size: 18px;
  font-weight: 600;

  & span {
    color: $basic-red;
  }
}
