.page-header {
  padding: 10px 0;

  background-color: $basic-white;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__list {
  @include reset-list;
  @include main-text;
  display: flex;
  align-items: center;
}

.header__list-item {
  margin-right: 40px;
  background-position: top left;
  background-repeat: no-repeat;
  padding-left: 50px;

  &:last-child {
    margin-right: 0;
  }
}

.header__list-item--plant {
  background-image: url("../img/plant.svg");
}

.header__list-item--buyers {
  background-image: url("../img/buyers.svg");
}

.header-phone {
  text-align: center;
}

.header-phone__text {
    @include main-text;
    margin-bottom: 10px;
    margin-top: 0;
}

.header-phone__btn {
  background-color: $basic-red;
  padding: 10px 30px;
  border-radius: 5px;
  text-decoration: none;
  background-image: url("../img/phone.svg");
  background-repeat: no-repeat;
  background-position: 14px center;
  color: $basic-white;
  font-weight: 500;

  &:hover {
    color: $basic-white;
    opacity: 0.8;
  }

  &:focus {
    outline: none;
    opacity: 0.8;
  }
}
