.symptoms {
  background-color: $basic-grey;
  padding: 70px;
}

.title {
  @include big-text;

  & span {
    color: $basic-red;
  }
}

.symptoms--wrapper {
  margin-top: 50px;
  display: grid;
  grid-template-columns: 1fr 1fr;

}

.symptoms__list {
  @include reset-list;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 100px;
}

.symptoms__list-item {
  background-image: url("../img/attention.svg");
  background-repeat: no-repeat;
  background-position: left 1px;
  padding-left: 50px;
}

.symptoms__header {
  margin-top: 30px;
  font-size: 22px;
  font-weight: 700;
  margin-bottom: 10px;
}

.symptoms__text {
  width: 420px;
  padding: 10px;
  border-radius: 5px;
  background-color: $grey;
}
