.main-screen {
  padding: 50px 0;

  background-color: $basic-white;
  background-image: url("../img/text.png");
  background-repeat: no-repeat;
  background-size: cover;
}

.main-screen--wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

.main-screen__title {
  text-align: center;
  font-size: 35px;
  font-weight: 700;
  margin-bottom: 20px;

  & span {
    color: $basic-red;
  }
}

.main-screen__subtitle {
  text-align: center;
  @include main-text;
  font-size: 18px;
  margin-bottom: 50px;
}

.main-screen__icons {
  @include reset-list;
  @include main-text;
  font-size: 16px;
}

.main-screen__icons-item {
  margin-bottom: 30px;
  background-position: top left;
  background-repeat: no-repeat;
  padding-left: 60px;

  &:last-child {
    margin-bottom: 0;
  }
}

.main-screen__icons-item--clock {
  background-image: url("../img/clock.svg");
}

.main-screen__icons-item--heart {
  background-image: url("../img/heart.svg");
}

.main-screen__icons-item--granny {
  background-image: url("../img/granny.svg");
}

.main-screen__icons-item--graphic {
  background-image: url("../img/graphic.svg");
}

.form {


  & form {
    padding: 20px;
    background-color: $light-grey;
    border-radius: 5px;
  }
}

.form__title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: $dark-grey;
  border-radius: 5px;
  padding: 20px;
}

.form__title-number {
  background-color: $basic-red;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  text-align: center;
  padding-top: 12px;
  font-weight: 700;
  color: $basic-white;
  margin: 0;
}

.form__title-text {
  color: $special-black;
  font-size: 14px;
  margin: 0;
}

.form-price {
  display: flex;
  justify-content: space-between;
  margin-top: 0;
  text-align: center;
}

.form-price--text {
  color: $special-black;

  & span {
    font-size: 18px;
    font-weight: 600;
  }

  &:last-child span {
    color: $basic-red;
  }
}

.form__label {
  font-size: 14px;
  color: $special-black;
  margin-top: 10px;
}

.form__input {
  width: 100%;
  padding: 10px;
  margin-top: 5px;
  font-size: 14px;
  margin-bottom: 10px;
  border: 1px solid $basic-grey;
}

.form__btn {
  background-color: $basic-red;
  border: none;
  border-radius: 5px;
  color: $basic-white;
  padding: 10px;
  width: 100%;
  margin-top: 20px;

  &:hover {
    color: $basic-white;
    opacity: 0.8;
  }

  &:focus {
    outline: none;
    opacity: 0.8;
  }
}

.form__text {
  margin-top: 20px;
  font-size: 14px;
  text-align: center;
}
