.advantages {
    margin-top: 70px;
    display: grid;
    grid-template-columns: 1fr 350px;
    grid-column-gap: 100px;

    & .title {
      margin-bottom: 40px;
    }
}

.advantages__text {
  @include main-text;
  margin-bottom: 20px;

  & span {
    color: $basic-red;
    font-weight: 600;
  }
}
