.expert {
  margin-top: 70px;
  padding: 70px 0 0;
  background-color: $light-grey;

  & .title {
    margin-bottom: 20px;
  }

  & img {
    margin-top: 40px;
  }
}

.expert--wrapper {
  display: grid;
  grid-template-columns: 400px 1fr;
  grid-column-gap: 50px;
}

.expert__text {
  @include main-text;

  & span {
    color: $basic-red;
    font-weight: 600;
  }
}

.expert__info {
    margin-top: 30px;
}

.expert__info-text {
  font-weight: 600;
  margin-bottom: 7px;
}
