.reasons {
  padding: 70px 0 40px;
  margin-top: 70px;
  background-color: $light-grey;

  & .title {
    text-align: center;
  }
}

.reasons__list {
  @include reset-list;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-top: 50px;
}

.reasons__item {
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 28%;
  background-repeat: no-repeat;
  background-position: center top;
  margin-bottom: 30px;
  padding-top: 130px;
}

.reasons__item-header {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 15px;
}

.reasons__item--angry {
  background-image: url("../img/angry.png");
}

.reasons__item--vessel {
  background-image: url("../img/veins.png");
}

.reasons__item--varicose {
  background-image: url("../img/varicos.png");
}

.reasons__item--sugar {
  background-image: url("../img/sugar.png");
}

.reasons__item--weight {
  background-image: url("../img/weight.png");
}

.reasons__item-subheader {
  @include main-text;

  & span {
    color: $basic-red;
    font-weight: 600;
  }
}
